
var _Date = {
    m30:       1800000, //30*60*1000
    hr24:      86400000, //24*60*60*1000
    GMToffset: new Date().getTimezoneOffset() * 60000,
    longLocaleStrOptions: { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' },

    timeStrToMS: function(str) {
        //'24.01.2019 08:00'
        var timeArr  = str.split(/[.:\s]/)
        var FTime_ms = timeArr.length > 2 ?
            Date.UTC(
                +timeArr[2],
                +timeArr[1]-1, // Month in JS starts from 0 !!!
                +timeArr[0],
                +timeArr[3],   //-3, // hours => per UTC !
                +timeArr[4] )
            :
            ( +timeArr[1] +timeArr[0]*60 ) * 60000
        return FTime_ms
    },

    dateMsToDateN: function(dateMs) {
        let dateN = Math.trunc( dateMs / 86400000 )
        return dateN
    },

    dateToYYYYMMDD: function(srcDate, delimeter) {
        delimeter   = delimeter || ''
        var year    =   srcDate.getFullYear(),
            month   =  +srcDate.getMonth()+1,
            day     =  +srcDate.getDate()
        var resDate = year + delimeter
                    + ( month>9 ? month : ('0' + month) ) + delimeter
                    + ( day>9   ? day   : ('0' + day)   )
        return resDate
    },

    dateToLongStrRu: (date) => date.toLocaleDateString('ru-RU', _Date.longLocaleStrOptions),

    dayNumToCustomDateObj: function(dayN) {
        let date    = new Date(dayN * _Date.hr24)
        let dateObj = _Date.customDate(date)
        return dateObj
    },

    msToSlotN: function(msTime) {
        let slotN = ( msTime - _Date.dateMsToDateN(msTime) * _Date.hr24 ) / _Date.m30
        return slotN
    },

    slotStrToMs: function(slotStr) {
        let [hh, mm,] = slotStr.split(/:/)
        let slotMs = ( +mm +hh*60 )*60000
        return slotMs
    },

    msToCustomDateObj: function(msTime, toggleGMT) {
    // var SSTime = (jsTime+3*60*60*1000)/1000/60/60/24+25569
        if(toggleGMT === true) {msTime -= _Date.GMToffset}
            let date    = new Date(msTime)
            let dateObj = _Date.customDate(date)
        return dateObj
    },// end parse msToCustomDateObj

    customDate: function( date ) {
        let timeMS   = date.getTime()
        let dateN    = _Date.dateMsToDateN(timeMS)
        let monthN   = date.getUTCMonth()
        let dayN     = date.getUTCDate()
        let timeStr  = `${date.getUTCHours()}:${ +date.getUTCMinutes() >= 30 ? '30' : '00'}`
        let dayWeekN = date.getUTCDay()
        let HDay     = _Date.isHoliday(monthN, dayN, dayWeekN)

        let dayName = ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб']
        let dateObj = {
            timeMS,
            timeStr,
            date,
            dateN,
            monthN: +monthN+1,
            dayN,
            dayName: dayName[dayWeekN],
            HDay
        }
        return dateObj
    },//end customDate

    isHoliday: function(monthN, dayN, dayWeekN) {
        var holidays2022 = [
            [3,4,5,6,7], //[0]JAN 2020
            [23],       //[1]FEB
            [7,8],           //[2]MAR
            [],            //[3]ARP
            [2,3,9,10],        //[4]MAY
            [13],          //[5]JUN
            [],[],[],[],   //JUL//AUG//SEP//OKT
            [4], []   //NOV//DEC
        ]

        return !!(
             dayWeekN == 0
          || dayWeekN == 6
          || holidays2022[monthN].indexOf(dayN)!= -1
        )
    }//end isHoliday

} // ===== END custom DATE OBJ ==========


const TIME_SLOTS = Object.freeze( getTimeSlotsArr() )
function getTimeSlotsArr() {
    let timeSlotsArr = []
    for (let i = 0; i <= 23; i++ ) {
        timeSlotsArr.push(
            [i*2*_Date.m30,            i*100+0,  i + ':00'],
            [i*2*_Date.m30 +_Date.m30, i*100+30, i + ':30']
        )
    }
    return(timeSlotsArr)
}


function setButtonText(buttonId, text) {
    if (!text || !buttonId) return
    let button = document.getElementById(buttonId)
    button.innerText = text
}


async function fetchAPI(url, reqObj) {
    const defaultOptions = {
        'method' : 'GET',
        'headers': {'Content-Type': 'application/x-www-form-urlencoded'},
        'body'   : new URLSearchParams()
    }

    var {params, options = defaultOptions} = reqObj

    if (options.method === 'POST') {
        for (const parameter in params){
            options.body.append(
                parameter,
                encodeURIComponent(params[parameter]) // may be POST request dont need it!
            )
        }
    } else {
        url += '?'
        for (const parameter in params){
            url += `&${parameter}=${encodeURIComponent(params[parameter])}`
        }
    }

    return fetch(url, options)
        .then( checkStatus )
        .then( retText )
        .then( proceedText )
        .catch( errorHandling )


        function checkStatus(response) {
            // console.dir('chStatus: ', response.status)
            if (!response.ok) {
                return Promise.reject( new Error(response.statusText) )
            }
            return Promise.resolve(response)
        }

        function retText(response) {
            // console.dir('retText')
            return response.text()
        }
        function proceedText(responseText) {
            console.dir(`Text fetched: ${responseText.slice(0,70)} ...`)
            return responseText
        }

        // function proceedProxyText(responseJson) {
        //     let respObj  = JSON.parse(responseJson)
        //         respObj  = respObj.contents

        //     responseJson = JSON.stringify(respObj)
        //                        .replace(/\\n/g, '\n')
        //     // console.dir(`Json replaced: ${responseJson.slice(0,400)}`);
        //     return responseJson
        // }


        function errorHandling(error) {
            console.dir("fetch err", error)
            return error
        }
}//===END ftch================================================



export {
    _Date,
    setButtonText,
    fetchAPI,
    TIME_SLOTS,
}