import { useEffect,
         useState,
         useRef,
         useMemo, }             from 'react'

import { useSelector,
         useDispatch,
         shallowEqual, }        from 'react-redux'

import { useForm,
         useController, }       from 'react-hook-form'


import { useBus,
         evDispatch, }          from 'JS/useBus'


import { SELECTORS,}            from 'index'

import { toggleFlyerEditorMD,
         flyerEditorStateUPD,
         bTableDateUPD,       } from 'JS/store'


import { _Date,
         TIME_SLOTS,}           from 'JS/services.mjs'



import { findFlyerData,
         Autocomplete, }        from '../FlyersEditor'




import { useRendersCount }      from 'JS/customHooks'



import { PAYMENT_ENUM,
         GET_INITIAL_VALUES,
         TARIF_MAP,
         codeDateTime,
         handleWatch,
         onKeyDown,
         closeSidebar,
         onSubmit }             from './bedHelpers'

import { StepInputButton,
         QuickInputBlock }      from './StepInputButton'

import { SubmitBlock }          from './SubmitBlock'



export const SUBMIT_BUTTONS_TEXT = {
    toCancel : 'Отменить бронирование',
    toMove   : 'Перенести бронирование',
    toChange : 'Изменить бронирование',
    toNew    : 'Записать как новое'
}

export const DEFAULT_BOOKING_VALS = {
    bookingId: '',
    history:   '',
    time:      0,
    timeVal:   0,
    tarif:     'ПР',
    fullname:  '',
    notes:     '',
    status:    '---',
    flyerId:   0,
    email:     '',
    phone:     '',
}


const MAIN_WATCHED_FIELDS = ['bookingId', 'flyerId', 'time', 'timeVal', 'tarif', 'status', 'notes']



function BookingEditor() {
    const renderCnt          = useRendersCount()
    const dispatch           = useDispatch()
    const valuesPassed       = useSelector(SELECTORS.sidebar_curObj)
    const flyersData         = useSelector(SELECTORS.flyersDataRaw)
    const flyerEditorState   = useSelector(SELECTORS.flyersData_editorState)
    const bookingEditorModal = useSelector(SELECTORS.booking_showModal)
    let  initialValues       = useMemo( () => GET_INITIAL_VALUES(valuesPassed, DEFAULT_BOOKING_VALS),[valuesPassed] )

    const { register,
        control,
        trigger,
        setValue,
        getValues,
        watch,
        handleSubmit,
        formState, }  = useForm({defaultValues: initialValues, mode: 'all'})

    const [flyerInput,
        setFlyerInput] = useState({ flyerId:  initialValues.flyerId,
                                    fullname: initialValues.fullname })

    register('flyerId', { validate: value => value !== 'TOSET' })

    // let debugValues = {flyerInput, initialValues, submitOptions: getValues('submitOptions')}

    // watch for flyerEditor submit flyer
    useEffect( () => {
    const {
        EVENT,
        flyerId,
        fullname
    } = flyerEditorState

    if (EVENT === 'SUBMITTED') {
        setFlyerInput({ flyerId, fullname })
        return
    }

    }, [dispatch, setValue, flyerEditorState])


    useEffect( () => {
    const flyerId = flyerInput.flyerId
    if (flyerId === 'TOSET') return
    if (flyerId === 'NEW') {
        setFlyerInput({ flyerId:  'TOSET'})//, fullname: ''})//flyerInput.fullname})
        dispatch( flyerEditorStateUPD({
            EVENT: 'TOSET',
            fullnameImputActive: false,
            fullname: flyerInput.fullname,
        }) )
        dispatch( toggleFlyerEditorMD(true) )
        return
    }

    const flyerData = findFlyerData({flyerId, flyersData})
    flyerData.forEach(
        field => setValue(field[0], field[1])
    )
    trigger('fullname')

    }, [dispatch, setValue, trigger, flyersData, flyerInput])




    const [Flight_Date, TimeSlot] = watch(['Flight_Date', 'TimeSlot'])
    useEffect(() => {
    if ( Flight_Date === initialValues['Flight_Date']
        && TimeSlot    === initialValues['TimeSlot'] ) return

    setValue( 'time', codeDateTime(Flight_Date, TimeSlot) )
    }, [Flight_Date, TimeSlot, initialValues, setValue])



    useEffect(() => {
    if (renderCnt <= 1) { return }
    let reqDate = _Date.dateMsToDateN( new Date(Flight_Date) )
    evDispatch(
        { type   : '@@ui/BOOKING_EDITOR_JUMPTODATE',
            payload: {reqDate} }
    )
    }, [Flight_Date, dispatch,])// eslint-disable-line




    const watchedMainValues = watch(MAIN_WATCHED_FIELDS)
    useEffect(() => {
    handleWatch({values: watchedMainValues,
                 watchedFields: MAIN_WATCHED_FIELDS,
                 initialValues,
                 setValue})
    }, [watchedMainValues, initialValues, setValue])


    console.log('BER RENDER', flyerInput, initialValues)
    // formState.errors && console.warn('BER RENDER BF ERRORS', formState.errors)//, getValues('submitOptions'))
    return (
    <div id="sidebar"
        className = "sidebar"
        onKeyDown = {onKeyDown}
        tabIndex  = "0"
    >
        <header className="sidebar-header">
            Бронирование
            <button id="closeSidebarBtn" onClick={closeSidebar}>X</button>
        </header>



        <form
            noValidate
            className = "side-form"
            onSubmit  = {handleSubmit(onSubmit)}
        >
            <div id="bookingIdDiv" className="side-form__inlineWrapper">
                <label id="bookingId-label">ID: </label>
                <input {...register("bookingId")}
                            disabled
                            type        = "number"
                            placeholder = " NEW"
                />
            </div>

            <label>Дата и время полёта</label>
            <div className="side-form__inlineWrapper">
                <input  {...register("Flight_Date", { required: true })}
                            type        = "date"
                            placeholder = "Flight_Date"
                />

                <select {...register("TimeSlot", { required: true })}
                            className = 'timeslot'
                >
                    {
                        TIME_SLOTS.map( (slot, i) => (
                            <option key={i} value={slot[2]}>{slot[2]}</option>
                        ))
                    }
                </select>
            </div>

            <div className="side-form__inlineWrapper">
                <label>
                    Минуты
                    <StepInputButton
                        controls   = {{getValues, setValue, trigger}}
                        inputName  = {'timeVal'}
                        step       = {+1}
                    />

                    <input {...register("timeVal", { required:      true,
                                                        valueAsNumber: true,
                                                        min:           1 } )}
                                type        = "number"
                                min         = "0"
                                onFocus     = {e => e.target.select()}
                    />

                    <StepInputButton
                        controls   = {{getValues, setValue, trigger}}
                        inputName  = {'timeVal'}
                        step       = {-1}
                        min        = {1}
                    />
                </label>

                <QuickInputBlock
                    controls   = {{setValue, trigger}}
                    inputName  = {'timeVal'}
                />

                <label>
                    Тариф
                    <select {...register("tarif", { required: true })}>
                        {
                            Object.entries(TARIF_MAP).map( (tarif) => {
                                const tarifName = tarif[1]
                                const tarifId   = tarif[0]
                                return <option key={tarifId} value={tarifName}>{tarifName}</option>
                            })
                        }
                    </select>
                    Оплата
                    <select {...register("status")}>
                        {
                            PAYMENT_ENUM.map( (option, i) => (
                                <option key={i} value={option}>{option}</option>
                            ))
                        }
                    </select>
                </label>
            </div>

            <label id="fullnameLabel" htmlFor="fullname">Клиент</label>
            <Autocomplete {...register("fullname", { required: true})}
                flyersData    = { flyersData }
                initValue     = { flyerInput.fullname }
                key           = { flyerInput.fullname }
                setFlyerInput = { setFlyerInput }
            />


            <label htmlFor="notes">Комментарии</label>
            <input type="text" {...register("notes", { required: false })} />


            <SubmitBlock
                control     = {control}
                setValue    = {setValue}
                getValues   = {getValues}
                formState   = {formState}
                buttonsText = { SUBMIT_BUTTONS_TEXT }
                // renderCnt = {renderCnt}
            />
        </form>

        {bookingEditorModal &&
                <div id='bookingEditorModal'>
                    {bookingEditorModal}
                </div>

        }
    </div>
    )
}

export default BookingEditor