export function GET_INITIAL_VALUES(valuesPassed = {}, defaultValues = {}) {
    const time = valuesPassed.time || Date.now()
    const submitOptions = valuesPassed.submitOptions || null
    const sumbitChoice = valuesPassed.sumbitChoice || ''
    const resultObj = {
        ...defaultValues,
        ...valuesPassed,
        ...parseDateTime(time),
        time,
        submitOptions,
        sumbitChoice
    };
    return resultObj
}

export function parseDateTime(dateTime) {
    const RecDate = new Date(dateTime || Date.now())
    const TimeSlotN = _Date.msToSlotN(dateTime)

    const Flight_Date = RecDate.toISOString().substring(0, 10)
    const TimeSlot = TIME_SLOTS[TimeSlotN]?.[2]

    return { Flight_Date, TimeSlot }
}

export function codeDateTime(date, slot) {
    return Date.parse(`${date} ${slot} GMT`)
}

export function handleWatch({ values, watchedFields, initialValues, setValue }) {
    const changedValues = watchedFields.reduce(
        (acc, field, index) => (
            values[index] === initialValues[field] ?
                acc :
                { ...acc, [field]: values[index] }
        ),
        {}
    )
    const isChanged = !!Object.entries(changedValues).length
    let submitOptions = initialValues.submitOptions ?
        initialValues.submitOptions :
        isChanged ?
            {
                toChange: !changedValues.time,
                toMove: !!changedValues.time,
                toNew: true
            }
            : { toCancel: true }
    setValue('submitOptions', submitOptions)

    if (!isChanged && !submitOptions.toMove ) { return }
    setValue('sumbitChanges', changedValues)

    //debugger
    // console.log(values, changedValues)
}



export const onSubmit = async (data) => {
    store.dispatch(bookingEditorModalUPD('ОТПРАВКА ЗАПРОСА \n\n НА СЕРВЕР \n\n ...'))
    let autoClose  = false
    let postAction = null
    let apiResponse


    const { sumbitChoice } = data
    const instrMode        = (data.status === 'Запрос')
    //debugger
    console.log('SUBMIT', data)
    data.sumbitChoice  = ''
    data.submitOptions = ''
    store.dispatch(sidebar_curObjUPD(data))

    if (sumbitChoice === 'toCancel') {
        apiResponse = await cancelBooking(data.bookingId, instrMode)
        autoClose   = true
    }

    if (sumbitChoice === 'toChange') {
        apiResponse = await changeBooking(data, instrMode)
    }

    if (sumbitChoice === 'toMove') {
        data = data.sumbitChanges.time
                ? data
                : { ...data, sumbitChanges: {time: data.time} }

        apiResponse = await changeBooking(data, instrMode)
    }

    if (sumbitChoice === 'toNew') {
        postAction = (rawBookingsData) => {
            // !!! Works only if shure that new id will be last in data array !
            const newBookingId = rawBookingsData.slice(-1)[0][0]
            evDispatch({type: '@@ui/EDIT_BOOKING_CALL', payload: newBookingId})
        }
        apiResponse = await addBooking(data, instrMode)
    }

    let noLS         = true
    let messageDelay = 500
    let bookingsObj  = {}
    let bookingsData = apiResponse


    if (bookingsData.status === 'OK') {
        store.dispatch(bookingEditorModalUPD('УСПЕШНО!'))
        bookingsData = bookingsData.data
        bookingsObj  = bookingsDataToObj(bookingsData)
        saveBookings(bookingsData, bookingsObj, noLS)
        if (postAction) {
            postAction(bookingsData)
        }
    } else {
        messageDelay = 3000
        store.dispatch(bookingEditorModalUPD(
            `ОШИБКА! \n
            ${apiResponse.status} \n
            ${JSON.stringify(apiResponse.data.content)}`
        ));
    }
    await new Promise((resolve) => setTimeout(resolve, messageDelay))
    store.dispatch(bookingEditorModalUPD(''))
    if (autoClose) { closeSidebar() }
}



async function cancelBooking(bookingId) {
    return API_CANCEL_BOOKING({ bookingId: bookingId })
}



async function changeBooking(bookingObj) {
    let { bookingId, sumbitChanges } = bookingObj

    if ('flyerId' in sumbitChanges) {
        const { email, fullname, phone } = bookingObj
        sumbitChanges = { ...sumbitChanges, email, fullname, phone }
    }

    if ('time' in sumbitChanges) {
        const dateTimeStr = `${bookingObj.Flight_Date} ${bookingObj.TimeSlot}`
        sumbitChanges = { ...sumbitChanges, dateTimeStr }
    }

    const changes = Array.from(Object.entries(sumbitChanges))

    console.log('CHANGE: ', bookingId, changes)
    return API_CHANGE_BOOKING({ bookingId, changes })
}



async function addBooking(bookingObj, instrMode = false) {
    const reqFieldsMap = APP_CONSTANTS.bookingsAPIDataMap;
    const dateTimeStr = `${bookingObj.Flight_Date} ${bookingObj.TimeSlot}`


    const bookingDataArr = [];
    Object.entries(reqFieldsMap).forEach(
        ([fieldName, fieldIndex]) => {
            bookingDataArr[fieldIndex] = bookingObj[fieldName.replace('CI', '')]
        }
    );

    bookingDataArr[reqFieldsMap.timeCI] = dateTimeStr

    console.log('ADD: ', JSON.stringify(bookingDataArr))
    return API_ADD_BOOKING({ bookingData: bookingDataArr,
                             instrMode })
}






export function closeSidebar() {
    store.dispatch(toggleSidebarMD(false));
    store.dispatch(sidebar_curObjUPD(GET_INITIAL_VALUES()))
}

export function onKeyDown(e) {
    if (e.key === 'Escape') {
        closeSidebar()
    }
}
