import { memo,
        useState,
        useEffect,
        useRef,
        useLayoutEffect,
        useImperativeHandle,
        forwardRef,
        Fragment }                 from 'react'

import { useSelector,
         useDispatch,
         shallowEqual,}            from 'react-redux'

import { APP_CONSTANTS,
         APP_VARS }                from '..'

import {_Date }                    from 'JS/services.mjs'

import { useIntersect,
         usePrevious }             from 'JS/customHooks.js'

import { store,
         bookingsDayDataUPD,
         bTableDateUPD,
         SELECTORS, }              from 'JS/store.js'

import { useBus, evDispatch, }     from 'JS/useBus'

export function InstrReqTab() {
    const USER          = useSelector(SELECTORS.userObj)
    const ADMIN_TOGGLE  = USER?.role === 'admin'
    const instrRequests = useSelector(SELECTORS.instrRequests)
    if (!instrRequests || Object.keys(instrRequests).length === 0) {return null}

    const requests = []
    Object.values(instrRequests).forEach(
        (reqValue) => {
            let { id, timeFlight, user, reqTime, } = reqValue

            if (ADMIN_TOGGLE || user === USER?.name) {
                reqTime = `${reqTime.slice(0,5).replace('.', '/')}  ${reqTime.slice(12,17)}`
                requests.push(
                    <option
                        key   = {id}
                        value = {JSON.stringify({id, timeFlight})}
                    >
                        { `${user} ${reqTime}:   ID:${id}` }
                    </option>
                )
            }
        }
    )
    if (!requests.length) { return null }

    const placeholder = <option
                            key   = "placeholder"
                            value = {requests[0]?.props?.value}
                        >
                                Запросов: {requests.length}
                        </option>

    const onChange = (e) => {
        const {id, timeFlight} = JSON.parse(e?.target?.value)
        console.log(id, new Date(timeFlight).toLocaleString())
        evDispatch({type: '@@ui/EDIT_BOOKING_CALL', payload: id})
        evDispatch({type: '@@ui/TEST_BTN3'        , payload: timeFlight})
    }

    return (
        <select
            id       = "instrRequests"
            onChange = {onChange}
        >
            {placeholder}
            {requests}
        </select>
    )
}