export async function REQ_GETBOOKINGS({stDate, endDate, days=5, validation, modification, source}) {
    stDate   = new Date(stDate)
    endDate  = new Date(endDate)

    if ( isNaN(stDate) )   stDate = new Date()
    if ( isNaN(endDate) ) {
        endDate = new Date(stDate)
        endDate.setHours( (days-1)*24, 0, 0, 0)
    }

    const requestObj  = {data: {stDate, endDate}}
    const command     = 'rasp'

    const responseObj = await API_REQUEST({requestObj, command, validation, modification, source})

    if (responseObj.status === 'OK') {
        let noLS         = true //false
        let bookingsData = responseObj.data;
        let bookingsObj  = bookingsDataToObj(bookingsData)
        return saveBookings(bookingsData, bookingsObj, noLS)
    }
    console.log( 'ERROR IN getBookingsData!')
    return null
}//end getBkngD


export async function getFlyersData ({validation, modification, source=DEFAULT_SOURCE}) {
    const responseObj = {status: 'LOADING', data: null}
    const USER        = getUser()

    let url, params, options

    switch (source) {
        case 'GAS_DEV_BS_URL':
        case 'GAS_MASTER_BS_URL':
            url     = APP_CONSTANTS[source]
            params  = {
                id: {
                    nick: USER.name,
                    mail: USER.mail,
                    ps: USER.ps,
                },
                command: 'getFlyers',
                data: {}
            }
            params.id   = JSON.stringify(params.id)
            params.data = JSON.stringify(params.data)
            options     = {'method': 'GET'}


            console.log('get flyers', params)

            validation = (fetchResult) => {
                return  !!(
                    fetchResult?.startsWith?.(`{"status":200,"content":`)
                    && fetchResult.length > 100
                )
            }
            modification = (bookingsData) => {
                bookingsData = JSON.parse(bookingsData)
                return bookingsData.content
            }
          break


        case 'TEST':
            url        = null
            params     = {}
            validation = () => false
          break
    }

    let flyersData = await fetchAPI(url, {params, options})

    if ( validation(flyersData) ) {
        responseObj.status = 'OK'
        responseObj.data   = flyersData
    } else {
        //debugger
        console.dir('fetched flyersData is INVALID!')//
        switch (TEST_MODE) {
            case true:
                responseObj.status = 'TEST'
                responseObj.data   =  await asyncGetTestData('flyers', {cnt: 100})
                return responseObj
            default:
                responseObj.status = 'ERROR: ' + responseObj.status
                responseObj.data   = flyersData
                return responseObj
        }
    }

    if (modification) {
        responseObj.data = modification(flyersData)
    }

    return responseObj
}

