export function saveBookings(bookingsData, bookingsObj, noLS = true) {
    if (!noLS) {
        window.localStorage.setItem('bookingsObj', JSON.stringify(bookingsObj));
    }
    store.dispatch(bookingsDataUPD({
        'obj': bookingsObj,
        'raw': bookingsData
    }))
}


export async function loadFlyersData() {
    let noLS       = true
    let flyersData = await getFlyersData({})

    if (flyersData.status === 'OK') {
        flyersData = flyersData.data;
        return saveFlyersData(flyersData, noLS)
    }
}

export function saveFlyersData(flyersData, noLS = true) {
    if (!noLS) {
        window.localStorage.setItem('bookingsObj', JSON.stringify(flyersData))
    }
    store.dispatch(flyersDataUPD(flyersData))
}





export async function API_REQUEST({requestObj, command, validation, modification, source = DEFAULT_SOURCE}) {
    console.log(`API_${command}`, requestObj)
    const responseObj = {status: 'LOADING', data: null}

    const USER   = getUser()

    let url, params, options

    switch (source) {
        case 'GAS_DEV_BS_URL':
        case 'GAS_MASTER_BS_URL':
            url     = APP_CONSTANTS[source]
            params  = {
                id: {
                    nick: USER.name,
                    mail: USER.mail,
                    ps: USER.ps,
                },
                command,
                data: requestObj
            }

            params.id   = JSON.stringify(params.id)
            params.data = JSON.stringify(params.data)
            options     = {'method': 'GET'}

            validation   = (fetchResult) => {
                return  !!(
                    fetchResult.startsWith?.(`{"status":200,"content":`)
                    && fetchResult.length > 100
                )
            }
            modification = (responseData) => {
                responseData = JSON.parse(responseData)
                return responseData.content
            }
          break
    }

    let responseData //= await fetchAPI(url, {params, options})
    try {
        responseData = await fetchAPI(url, {params, options})
    } catch (error) {
        responseData = error
    }

    if ( validation(responseData) ) {
        responseObj.status = 'OK'
        responseObj.data   = responseData
    } else {
        console.dir(`API_${command} request is INVALID!`)//
        responseObj.status = 'ERROR: ' + responseObj.status

        try {
            responseObj.data = JSON.parse(responseData)
        } catch (error) {
            responseObj.data = responseData
        }

        return responseObj
    }

    if (modification) {
        responseObj.data = modification(responseData)
    }

    return responseObj
}



export async function API_CANCEL_BOOKING({bookingId}) {
    const requestObj = {bookingId}
    const command    = 'cancelBooking'

    return await API_REQUEST({requestObj, command})
}


export async function API_CHANGE_BOOKING(bookingObj) {
    const command    = 'changeBooking'

    return await API_REQUEST({requestObj: bookingObj, command})
}

export async function API_ADD_BOOKING(bookingObj) {
    const instrMode  = !!bookingObj.instrMode
    const command    = instrMode ? 'addBooking_instrMode' : 'addBooking'

    return await API_REQUEST({requestObj: bookingObj, command})
}


export async function API_ADD_FLYER(flyerObj) {
    const command    = 'addFlyer'
    return await API_REQUEST({requestObj: flyerObj, command})
}


export async function HANDLE_API_BOOKING_RESPONSE(responseObj) {
    let noLS         = true
    let bookingsObj  = {}
    let bookingsData = responseObj


    if (bookingsData.status === 'OK') {
        bookingsData = bookingsData.data;
        bookingsObj  = bookingsDataToObj(bookingsData)
        // return saveBookings(bookingsData, bookingsObj, noLS);
    }
}






