export function StepInputButton({ controls, inputName, step, min, max, children }) {
    const { getValues, setValue, trigger } = controls;
    const className = step > 0
        ? 'stepInputButton'
        : 'stepInputButton stepInputButton--bottom';
    const text = children || step > 0 ? '+' : '-';

    const stepAction = (e) => {
        const newValue = getValues(inputName) + step;

        if (min && newValue < min)
            return;
        if (max && newValue > max)
            return;

        setValue(inputName, newValue);
        trigger(inputName);
    };

    return <>
        <button
            type="button"
            className={className}
            onClick={stepAction}
        >
            {text}
        </button>
    </>;
}
export function QuickInputBlock({ controls, inputName }) {
    const { setValue, trigger } = controls;
    const className = 'quickInputBlock';
    const values = [5, 10, 15, 30];

    const quickAction = (e) => {
        const value = e.target.dataset.value;
        setValue(inputName, value);
        trigger(inputName);
    };

    return <div className={className}>
        {values.map(value => (
            <button
                key={value}
                type="button"
                data-value={value}
                onClick={quickAction}
                className={className + '-button'}
            >
                {value}
            </button>
        ))}
    </div>;
}
